/**
 * Returns a promise that resolves and the passed number of milliseconds.
 * This utility is an async/await-friendly version of `setTimeout`.
 *
 * @param {number} ms
 * @return {Promise}
 * @private
 */
export function timeout(ms) {
    return new Promise(resolve => setTimeout(resolve, ms));
}
/**
 * Checks if the passed request is an HTTP request.
 *
 * @param {Request | URL} request
 * @return {boolean}
 * @private
 */
export const isHttpRequest = (request) => {
    if (request instanceof Request) {
        return request.url.startsWith('http');
    }
    return request.toString().startsWith('http');
};
/**
 * Converts a response to JSON.
 */
export const toJSON = async (response) => {
    if (response instanceof Response) {
        return await response.clone().json();
    }
    return response;
};
